import React, { Component } from 'react';

class Homepage extends Component {
  render() {
    return (
      <div className="container-fluid" id='background'>
        <div id='mainText'>
          <h1>Solid. <sub>Soap</sub></h1>
        </div>
      </div>
    );
  }
}

export default Homepage;


